import StockMovementHead from "./components/stockHead";
import StockMovementTable from "./components/stockTable";

export default function StockMovementScreen() {
    return (
        <div className="flex flex-col gap-4 p-8 w-full h-full border">
            <div className="h-full flex-[10%]">
                <StockMovementHead />
            </div>
            <div className=" h-full flex-[90%]">
                <StockMovementTable />
            </div>
        </div>
    );
}
